import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
// Components

// Routes
import Authetications, { AutheticationPageLists } from './modules/Authetications';
import Access from './modules/Access';

const Routers = () => {
    const location = useLocation();
    const [isGeneralRoutes, setGeneralRoutes] = useState(true);
    const [page, setPage] = useState('Dashboard');

    useEffect(() => {
        const arraySet = location.pathname.split('/');
        setPage(arraySet[1]);
        if (AutheticationPageLists.includes(location.pathname) || AutheticationPageLists.includes(arraySet[1])) {
            setGeneralRoutes(true);
        } else {
            setGeneralRoutes(false);
        }
    }, [location]);

    return <>{isGeneralRoutes ? <Authetications /> : <Access />}</>;
};

export default Routers;
